<template>
  <div class="home">
    <p id="head">LA<br>NUIT<br>GLAMOUR</p>
    <p id="sub-head">The Official GESA Dinner</p>

    <div id="date-venue">
      <p id="date">20th April</p>
      <div class="v-divider"></div>
      <p id="venue">The Cove<br>Event Center</p>
    </div>

    <router-link to="/" id="vote-link">VOTING IS OVER!</router-link>
    <p>Countdown to dinner</p>

    <div id="countdown">
      <div class="v-align">
        <div class="v-align-val">{{days}}</div>
        <div class="v-align-label">DAYS</div>
      </div>
      <span class="countdown-divider">: </span>
      <div class="v-align">
        <div class="v-align-val">{{hours}}</div>
        <div class="v-align-label">HOURS</div>
      </div>
      <span class="countdown-divider">: </span>
      <div class="v-align">
        <div class="v-align-val">{{minutes}}</div>
        <div class="v-align-label">MINUTES</div>
      </div>
      <p id="countdown-seconds">
        {{seconds}}s
      </p>
    </div>

    <div id="foot">
      <p>
        For <b>reservations</b> or more infromation contact the office of the Ghana Engineering Student Association, KNUST on <b>+233 26 7498 084</b>.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data: () => ({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
    countdownDate: 0,
  }),
  methods: {},
  created(){
    // get the countdown date
    this.countdownDate = 1555779600000
    // run countdown logic
    setTimeout(() => {
      var x = setInterval(() => {
        let now = new Date().getTime();
        let distance = this.countdownDate - now;
  
        let days = Math.floor(distance / (1000 * 60 * 60 * 24));
        let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        let seconds = Math.floor((distance % (1000 * 60)) / 1000);
  
        this.days = days;
        this.hours = hours;
        this.minutes = minutes;
        this.seconds = seconds;
  
        // If the count down is finished, write some text 
        if (distance < 0) {
          clearInterval(x);
          document.getElementById("countdown").innerHTML = "SEE YOU @ THE COVE EVENT CENTER";
        }
      }, 1000);
    }, 1200);
  }
}
</script>

